<template>
  <!-- <associate-object-streams
    title="Streams"
    :items="streams"
    :onCancel="onCancel"
    :streamId="flowNetworkId"
    :apiListKey="apiListKey"
    :service="service"
  >
  </associate-object-streams> -->

  <flow-mapping-form
    v-if="currentItems"
    :items="streams"
    :currentItems="currentItems"
    :onSave="onSave"
    title="Associate Streams"
    :onCancel="onCancel"
  ></flow-mapping-form>
</template>
<script>
import FlowMappingForm from './flowMappingForm.vue';
import AssociateObjectStreams from './associateObjectStreams.vue';
import { rfFlowNetworkService } from '../../../services/rfFlowNetworkService';

export default {
  name: 'associate-streams-form',
  props: ['flowNetworkId', 'onCancel'],
  components: { AssociateObjectStreams, FlowMappingForm },
  data() {
    return {
      apiListKey: 'streams',
      service: rfFlowNetworkService,
      currentItems: null,
    };
  },
  computed: {
    streams() {
      return this.$store.state.rfStreams;
    },
  },
  mounted() {
    this.fetchFlowNetworkById();
  },

  methods: {
    onSave(data) {
      return rfFlowNetworkService.patch(this.flowNetworkId, { streams: data });
    },
    fetchFlowNetworkById() {
      return rfFlowNetworkService.fetchById(this.flowNetworkId).then(data => {
        this.currentItems = data.streams || [];
        return data;
      });
    },
  },
};
</script>
