<template>
  <div>
    <generic-grid :headers="gridHeaders" :toolbarTitle="toolbarTitle" :service="service">
      <template v-slot:createForm="{ onSave, onClose, isOpen, isSaving }">
        <flow-network-form
          v-if="isOpen"
          :isEdit="false"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></flow-network-form>
      </template>
      <template v-slot:editForm="{ onSave, item, onClose, isOpen, isSaving }">
        <flow-network-form
          v-if="isOpen"
          :item="item"
          :isEdit="true"
          :onSave="onSave"
          :onCancel="onClose"
          :isSaving="isSaving"
        ></flow-network-form>
      </template>
      <template v-slot:gridItemActions="{ item }">
        <icon-button
          title="Add Streams"
          icon="mdi-plus"
          color="nubeGreen"
          :onClick="() => toggleDialog(dialogNames.associateStreams, item)"
        ></icon-button>
      </template>
    </generic-grid>

    <v-dialog
      persistent
      max-width="500px"
      v-if="selectedItem && selectedItem.uuid"
      v-model="dialog[dialogNames.associateStreams]"
    >
      <associate-streams
        v-if="dialog[dialogNames.associateStreams]"
        :flowNetworkId="selectedItem && selectedItem.uuid"
        :onCancel="() => closeDialog(dialogNames.associateStreams)"
      ></associate-streams>
    </v-dialog>
  </div>
</template>
<script>
import FlowNetworkForm from './flowNetworkForm.vue';
import GenericGrid from '../../Common/GenericGrid.vue';
import { rfFlowNetworkService } from '../../../services/rfFlowNetworkService';
import IconButton from '../../Common/IconButton.vue';
import AssociateStreams from '../MapStreamFlowNetwork/associateStreams.vue';

const dialogNames = {
  associateStreams: 'associateStreams',
};

export default {
  components: { GenericGrid, FlowNetworkForm, IconButton, AssociateStreams },
  name: 'flowNetworkGrid',
  data() {
    return {
      service: rfFlowNetworkService,
      toolbarTitle: 'Flow Networks',
      selectedItem: {},
      dialogNames: { ...dialogNames },
      dialog: {
        [dialogNames.associateStreams]: false,
      },
      selectedItem: {},
      gridHeaders: [
        {
          text: 'Name',
          align: 'center',
          value: 'name',
          sortable: true,
        },
        {
          text: 'Remote Flow Id',
          align: 'center',
          value: 'remote_flow_uuid',
          sortable: true,
        },
        {
          text: 'Global Flow Id',
          align: 'center',
          value: 'global_flow_id',
          sortable: true,
        },
        {
          text: 'Grid Actions',
          align: 'center',
          value: 'gridItemActions',
          sortable: false,
        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: true,
        },
      ],
    };
  },
  methods: {
    toggleDialog(dialogName, item) {
      this.selectedItem = item;
      this.dialog[dialogName] = !this.dialog[dialogName];

      if (this.dialog[dialogName]) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
      this.selectedItem = {};
    },
  },
};
</script>
